import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'
import ExperienceConsultation from '../views/ExperienceConsultation.vue' // 体验+咨询
import ImplementationPlan from '../views/ImplementationPlan.vue' // 方案+实施
import CaseLanding from '../views/CaseLanding.vue' // 案例+落地
import About from '../views/About.vue' // 关于我们
import PlatformTeam from '../views/PlatformTeam.vue' // 平台+团队
import NewExperience from '../views/NewExperience.vue' // 新体验

// Home里面的三个界面搭建
import DevelopGameCourseware from '../views/Home/DevelopGameCourseware.vue'
import AutoCourseware from '../views/Home/AutoCourseware.vue'
import DigitalMidrangeSystem from '../views/Home/DigitalMidrangeSystem.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/home',
    name: 'Home',
    component: Home,
    children: [
      {
        path: 'gamecourse',
        name: 'DevelopGameCourseware',
        component: DevelopGameCourseware
      },
      {
        path: 'autocourse',
        name: 'AutoCourseware',
        component: AutoCourseware
      }, {
        path: 'digitalsystem',
        name: 'DigitalMidrangeSystem',
        component: DigitalMidrangeSystem
      },
      {
        path: '',
        redirect: 'gamecourse'
      }
    ]
  },
  {
    path: '/experience',
    name: 'ExperienceConsultation',
    component: ExperienceConsultation
  },
  {
    path: '/implementation',
    name: 'ImplementationPlan',
    component: ImplementationPlan
  },
  {
    path: '/caselanding',
    name: 'CaseLanding',
    component: CaseLanding
  },
  {
    path: '/about',
    name: 'About',
    component: About
  },
  {
    path: '/platformteam',
    name: 'PlatformTeam',
    component: PlatformTeam
  },
  {
    path: '/newexperience',
    name: 'NewExperience',
    component: NewExperience
  },
  {
    path: '*',
    redirect: '/home'
  }
]

const router = new VueRouter({
  // mode: 'history',
  // base: process.env.BASE_URL,
  routes
})

export default router
